.contacts-container {
    position: relative;
    background-image: linear-gradient(#6096BA, #274C77);
    
    /* swapping the value from bottom break to top */
    --witdthA: 100%;
    --widthB: calc(100% - 5vh);
    --widthActual: calc(var(--witdthA) - var(--widthB));
    clip-path: polygon(
    0 0,
    100% var(--widthActual),
    100% 100%,
    0 100%
    );
    
    padding-top: 50px;
    margin-top: 100px;
}

.contact-title {
    color: white;
    margin-top: 150px;
    font-size: 2.5rem;
    margin-bottom: 3rem;
}

.contact-text {
    font-size: 1.5rem;
    text-align: center;
    color: white;
    padding-bottom: 1rem;
}

.contact-links {
    display: flex;
    justify-content: center;
    padding-top: 10px;
    padding-bottom: 40px;
}

.icon {
    padding: 0 10px;
    transition: 0.3s ease;
}

.icon:hover {
    fill: rgba(255,255,255,0.5) !important;
}

.button-container {
    display: flex;
    justify-content: center;
}

.random-button {
    padding: 15px 60px;
    border-radius: 45px;
    border-style: none;
    margin-bottom: 40px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    outline: none;
    cursor: pointer;
    transition: 0.3s ease;
    background-color: #E7ECEF;
}

.random-button:hover {
    transform: translateY(-5px);
}

.message-wrapper {
    display: flex;
    justify-content: center;
}

.message {
    padding: 20px 60px;
    background-color: #E7ECEF;
    border-radius: 50px;
    margin-bottom: 50px;
}
